CA,sBAtDiB,CAyDnB,2DACE,qC7B5CO,C6B6CP,eAAA,CACA,eAAA,CACA,qB7BjCU,C6BsChB,+BACE,iBAAA,CACA,YAAA,CACA,kBAAA,CACA,sBAAA,CACA,kBAAA,CACA,WAAA,CACA,WAAA,CAEA,+CACE,kCAAA,CACA,aA9Ec,CA+Ed,eAAA,CACA,eAAA,CACA,iBAAA,CACA,0BAAA,CAGF,0CACE,YAAA,CACA,kBAAA,CACA,kBAAA,CACA,WAAA,CAEA,gDACE,WAAA,CACA,qBAAA,CACA,UAAA,CAKN,+BACE,iBAAA,CACA,YAAA,CACA,kBAAA,CACA,kBAAA,CACA,WAAA,CACA,WAAA,CAEA,+CACE,kCAAA,CACA,aA7Gc,CA8Gd,eAAA,CACA,sBA9GmB,CA+GnB,gBAAA,CACA,gBAAA,CACA,0BAAA,CAGF,yCACE,YAAA,CACA,kBAAA,CACA,kBAAA,CACA,WAAA,CAEA,+CACE,WAAA,CACA,qBAAA,CACA,UAAA,CAAA","sourcesContent":["/**\r\n * Import all the fonts used in this project.\r\n */\r\n\r\n@import url('https://fonts.googleapis.com/css2?family=Libre+Franklin:wght@200;300;400&family=Overpass+Mono:wght@300;400;600;700&display=swap');\r\n@import url('https://use.typekit.net/zhb6cru.css');\r\n@import url('https://fonts.googleapis.com/css2?family=Overpass:ital@0;1&display=swap');\r\n\r\n@font-face {\r\n    font-family: 'Sprat Type Condensed';\r\n    src: url('../../assets/fonts/sprat-type/Sprat-CondensedLight.otf');\r\n    font-weight: 400;\r\n}\r\n\r\n@font-face {\r\n    font-family: 'Sprat Type Condensed';\r\n    src: url('../../assets/fonts/sprat-type/Sprat-CondensedRegular.otf');\r\n    font-weight: 600;\r\n}\r\n\r\n// Font families\r\n$FONT_DISPLAY: 'Sprat Type Condensed';\r\n$FONT_SUBHEAD: 'Overpass Mono', monospace;\r\n$FONT_BODY: 'franklin-gothic-urw', sans-serif;;\r\n\r\n// Font sizes\r\n$FONT_SIZE_NAV: 2em; // changed from 38px\r\n$FONT_SIZE_LINK: 2vh; //changed from 24px\r\n$FONT_SIZE_LINE: 12vh; // changed from 114px\r\n$FONT_SIZE_DESIGNER: 3vh;\r\n$FONT_SIZE_TITLE: 48px;\r\n$FONT_SIZE_BODY: 23px;\r\n$FONT_SIZE_LABEL: $FONT_SIZE_LINK;\r\n$FONT_SIZE_LINE_PAGE_NAME: 12vh;\r\n\r\n// font adjustments and weights\r\n$FONT_SPRAT_KERN: -0.1em;\r\n$FONT_SPRAT_WEIGHT: 200;\r\n$FONT_WEIGHT_LIGHT: 400;\r\n$FONT_TITLE_WEIGHT: 400;\r\n$FONT_LINK_WEIGHT: 400;\r\n//$FONT_LABEL_WEIGHT: 400;\r\n$FONT_WEIGHT_REGULAR: 600;","/**\r\n * Styles shared between mobile and desktop\r\n */\r\n\r\n.logo {\r\n  fill: white;\r\n}\r\n\r\nbody {\r\n  margin: 0;\r\n  background-color: $LANDING_PAGE_BACKGROUND_COLOR;\r\n  font-family: 'Sprat Type Condensed';\r\n}\r\n\r\na {\r\n  color: white;\r\n}\r\n\r\n.unbreakable-name {\r\n  white-space: nowrap;\r\n}\r\n\r\n.preserve-whitespace {\r\n  white-space: pre;\r\n}","/**\r\n * Constants for stylesheet\r\n */\r\n\r\n// sizing\r\n$X_MARGIN: 10vw;\r\n$BOTTOM_MARGIN: 10vh;\r\n$TOP_MARGIN: 36px;\r\n$DESKTOP_WIDTH: 768px;\r\n\r\n$TOP_MARGIN_MOBILE: 15px;\r\n\r\n/**\r\n * NOTE: this is completely arbitrary and loosely based upon values at\r\n * https://www.kylejlarson.com/blog/ipad-screen-size-guide-web-design-tips/.\r\n * - Desktop readjusted to 1240 since the navbar starts looking a bit squished\r\n * - Tablet set to 720 to be below minimum iPad browser width\r\n */\r\n$TABLET_WIDTH: 672px;\r\n$LINE_ELEMENT_HEIGHT: 142px;\r\n$NAV_ELEMENT_HEIGHT: 126px;\r\n\r\n// letter spacing\r\n$HEADER_TRACKING: -0.11em;\r\n$TITLE_TRACKING: -5px;\r\n$SUBHEAD_TRACKING: -2px;\r\n\r\n// Word spacing\r\n$HEADER_WORD_SPACING: 0.1em;\r\n\r\n// Background color\r\n$LANDING_PAGE_BACKGROUND_COLOR: #000000;\r\n$BACKGROUND_COLOR: #000000;\r\n\r\n// Keep track of z-indices\r\n$Z_INDEX_GOD: 999; /* highly discouraged to use */\r\n$Z_INDEX_HEADER: 10;\r\n$Z_INDEX_MAIN_UI: 3;\r\n$Z_INDEX_BACKGROUND: -1;\r\n\r\n// Animation durations\r\n$SHORT_ANIMATION_DURATION: 200ms;\r\n$MEDIUM_ANIMATION_DURATION: 300ms;\r\n$LONG_ANIMATION_DURATION: 1000ms;","/**\r\n * Common animations shared among many elements.\r\n *\r\n * For each animation, we provide some constants, and also a class that an\r\n * element can add so it can trigger the animation.\r\n */\r\n\r\n$ANIMATION_DURATION_BLUR: 500ms;\r\n@keyframes blur-in {\r\n  0% {\r\n    opacity: 0;\r\n    filter: blur(5px);\r\n  }\r\n  10% {\r\n    opacity: 0.75;\r\n  }\r\n  100% {\r\n    opacity: 1;\r\n    filter: blur(0);\r\n  }\r\n}\r\n\r\n.blur-in-animation {\r\n  animation: blur-in $ANIMATION_DURATION_BLUR linear forwards;\r\n}\r\n\r\n$ANIMATION_DURATION_FADE_OUT: 500ms;\r\n@keyframes fade-out {\r\n  from {\r\n    opacity: 1;\r\n  }\r\n\r\n  to {\r\n    opacity: 0;\r\n  }\r\n}\r\n\r\n.fade-out-animation {\r\n  animation: fade-out $ANIMATION_DURATION_FADE_OUT linear forwards;\r\n}\r\n\r\n$ANIMATION_DURATION_SLIDE_IN: 500ms;\r\n@keyframes right-bar-slide-in {\r\n  from {\r\n    transform: translateX(100%);\r\n  }\r\n\r\n  to {\r\n    transform: translateX(0);\r\n  }\r\n}\r\n\r\n.right-bar-slide-in-animation {\r\n  animation: right-bar-slide-in $ANIMATION_DURATION_SLIDE_IN forwards;\r\n}\r\n\r\n@keyframes left-bar-slide-in {\r\n  from {\r\n    transform: translateX(-100%);\r\n  }\r\n\r\n  to {\r\n    transform: translateX(0);\r\n  }\r\n}\r\n\r\n.left-bar-slide-in-animation {\r\n  animation: left-bar-slide-in $ANIMATION_DURATION_SLIDE_IN forwards;\r\n}\r\n\r\n$ANIMATION_DURATION_FADE: 1s;\r\n.fade-enter {\r\n  opacity: 0;\r\n}\r\n.fade-enter-active {\r\n  opacity: 1;\r\n  transition: opacity $ANIMATION_DURATION_FADE ease-in;\r\n}\r\n.fade-exit {\r\n  opacity: 1;\r\n}\r\n.fade-exit-active {\r\n  opacity: 0;\r\n  transition: opacity $ANIMATION_DURATION_FADE ease-in;\r\n}","/**\r\n * Compiles all the styles together\r\n */\r\n\r\n@use 'sass:math' as *;\r\n\r\n// Shared all screens\r\n@import './constants.scss';\r\n@import './fonts.scss';\r\n@import './common.scss';\r\n@import './desktop/animations/common_animations.scss';\r\n\r\n// Desktop styles\r\n@media (min-width: $DESKTOP_WIDTH) {\r\n  @import './desktop/desktop_constants.scss';\r\n  @import './desktop/index.scss';\r\n  @import './desktop/components/dots_lines.scss';\r\n  @import './desktop/components/landing_page.scss';\r\n  @import './desktop/components/about_page.scss';\r\n  @import './desktop/components/watch_page.scss';\r\n  @import './desktop/components/navbar.scss';\r\n  @import './desktop/components/navbar_line_page.scss';\r\n  @import './desktop/components/desktop_side_nav.scss';\r\n  @import './desktop/components/landing_page_prompt.scss';\r\n  @import './desktop/pages/line_page.scss';\r\n  @import './desktop/components/people_page.scss';\r\n  @import './desktop/animations/navbar_animations.scss';\r\n  @import './desktop/animations/vertical_line_animations.scss';\r\n  @import './desktop/animations/prompt_animations.scss';\r\n}\r\n\r\n// Mobile styles\r\n@media (max-width: $DESKTOP_WIDTH - 1) {\r\n  @import './mobile/mobile_constants.scss';\r\n  @import './mobile/index.scss';\r\n  @import './mobile/components/landing_page.scss';\r\n  @import './mobile/pages/line_page.scss';\r\n  @import './mobile/components/about_page.scss';\r\n  @import './mobile/components/dots_lines.scss';\r\n  @import './mobile/components/mobile_open_menu.scss';\r\n  @import './mobile/components/mobile_menu_line_list.scss';\r\n  @import './mobile/components/mobile_menu_nav_list.scss';\r\n  @import './mobile/pages/people_page.scss';\r\n  @import './mobile/pages/watch_page.scss';\r\n}","/**\r\n * Styles for the main site for DESKTOP\r\n */\r\n\r\n// lines and accents\r\n$CIRCLE_RAD : 12px;\r\n$OUTER : $X_MARGIN/8; // outer margin for vertical lines\r\n$LOWER : $BOTTOM_MARGIN/4;\r\n$UPPER : $BOTTOM_MARGIN*2.2;\r\n$LOGO_SIZE: $FONT_SIZE_TITLE - 8px;\r\n\r\nbody {\r\n  color: white;\r\n  height: 100vh;\r\n\twidth: 100%;\r\n\t-ms-overflow-style: none;  /* IE and Edge */\r\n\tscrollbar-width: none;  /* Firefox */\r\n\r\n\t&::-webkit-scrollbar {\r\n\t\tdisplay: none;\r\n\t}\r\n\r\n\toverflow-x: hidden;\r\n}\r\n\r\n.mobile {\r\n  display: none;\r\n}\r\n\r\n.main-page {\r\n\tdisplay: flex;\r\n\tflex-direction: column;\r\n\talign-items: center;\r\n\tjustify-content: center;\r\n}\r\n\r\n/* Display font */\r\nh1{\r\n  font-family: 'Sprat Type Condensed';\r\n  font-weight: 100;\r\n\tfont-size: 4em;\r\n\tletter-spacing: -.11em;\r\n\tword-spacing: .1em;\r\n\tmargin-bottom: 0;\r\n\ttext-transform: uppercase;\r\n}\r\n\r\n/* Subheader 1 */\r\nh2 {\r\n  font-family: 'Sprat Type Condensed';\r\n  font-weight: normal;\r\n\tfont-size: 1.5em;\r\n\tletter-spacing: -.01em;\r\n\tmargin-top: 0;\r\n\tmargin-bottom: 1em;\r\n\ttext-transform: uppercase;\r\n}\r\n\r\n/* Subheader 2 */\r\nh3 {\r\n\tfont-family: 'Overpass Mono', monospace;\r\n\tfont-weight: 400;\r\n\tfont-size: 1em;\r\n\tletter-spacing: -0.05em;\r\n\tmargin-bottom: 0;\r\n\tmargin-top: 0;\r\n\ttext-transform: uppercase;\r\n}\r\n\r\n/* Body text */\r\np {\r\n\tfont-family: 'franklin-gothic-urw', sans-serif;\r\n\tfont-weight: 400;\r\n\tfont-style: normal;\r\n\tfont-size: 1em;\r\n\tmargin-top: 0.5em;\r\n}\r\n\r\n// Common elements\r\n.title-theme {\r\n\tposition: fixed;\r\n\tz-index: $Z_INDEX_GOD;\r\n\ttop: $TOP_MARGIN;\r\n\topacity: 0;\r\n\ttransition-duration: 1s;\r\n\tdisplay: flex;\r\n\tflex-direction: row;\r\n\talign-items: center;\r\n\tcursor: pointer;\r\n\tpointer-events: none;\r\n\r\n\t// Title is not visible when no line is selected\r\n\t&.start-animation {\r\n\t\tanimation: blur-in $ANIMATION_DURATION_BLUR forwards;\r\n\t}\r\n\t&.visible {\r\n\t\topacity: 1;\r\n\t\tpointer-events: all;\r\n\t}\r\n\r\n\tspan {\r\n\t\tfont-size: $FONT_SIZE_TITLE;\r\n\t\tfont-family: 'Sprat Type Condensed';\r\n\t\tfont-weight: 300;\r\n\t\tletter-spacing: $TITLE_TRACKING;\r\n\t}\r\n\r\n\t#collectiva-logo {\r\n\t\tmargin-top: 4px;\r\n\t\tmargin-left: 12px;\r\n\t\theight: $LOGO_SIZE;\r\n\t\twidth: $LOGO_SIZE;\r\n\r\n\t\tsvg {\r\n\t\t\theight: 100%;\r\n\t\t\twidth: 100%;\r\n\t\t}\r\n\t}\r\n}","/**\r\n * Style definitions for overlayed lines and dots.\r\n */\r\n\r\n// TODO: this dot is poorly defined because it has too many specific variables,\r\n// making it hard to extend and use in general. See .dot-basic.\r\n.dot {\r\n  width: $CIRCLE_RAD;\r\n  height: $CIRCLE_RAD;\r\n  background-color: white;\r\n  border-radius: 100%;\r\n  position: absolute;\r\n  margin-bottom: -$CIRCLE_RAD/2;\r\n}\r\n\r\n.dot-basic {\r\n  width: $CIRCLE_RAD;\r\n  height: $CIRCLE_RAD;\r\n  background-color: white;\r\n  border-radius: 100%;\r\n}\r\n\r\n.line {\r\n  height: 1px;\r\n  background-color: white;\r\n}\r\n\r\n.vertical-line {\r\n  height: 100vh;\r\n  top: 0;\r\n  position: fixed;\r\n  border-left: 1px solid white;\r\n  border-right: 1px solid white;\r\n  pointer-events: none;\r\n  opacity: 0;\r\n}\r\n\r\n#outer-lines {\r\n  left: $OUTER;\r\n  right: $OUTER;\r\n}\r\n\r\n#inner-lines {\r\n  left: $X_MARGIN/4;\r\n  right: $X_MARGIN/4;\r\n}\r\n\r\n.lower {\r\n  bottom: 0;\r\n}\r\n\r\n.upper {\r\n  top: -$CIRCLE_RAD/2;\r\n}\r\n\r\n.left-dot {\r\n  left: $OUTER;\r\n  margin-left: -$CIRCLE_RAD/2;\r\n}\r\n\r\n.right-dot {\r\n  right: $OUTER;\r\n  margin-right: -$CIRCLE_RAD/2;\r\n}","/**\r\n * First page the user sees, the landing page.\r\n *\r\n * Contains a 3D asset in the back.\r\n */\r\n\r\n$DESIGNERS_NAME_HEIGHT: 46px;\r\n$SEE_MORE_WRAPPER_MARGIN: 1.25vw;\r\n$ARROW_WIDTH: 14px;\r\n$SEE_MORE_BOTTOM_MARGIN: 12px;\r\n\r\n.landing-page-background {\r\n  background-color: black;\r\n  width: 100%;\r\n  height: 100vh;\r\n  opacity: 0;\r\n  transition-duration: $LONG_ANIMATION_DURATION;\r\n  position: fixed;\r\n  top: 0;\r\n  left: 0;\r\n\r\n  &.desktop-landing-page-load {\r\n    opacity: 0;\r\n  }\r\n\r\n  &.desktop-people-page-open {\r\n    transition-duration: 0s;\r\n  }\r\n\r\n  &.visible {\r\n    opacity: 1;\r\n  }\r\n\r\n  #landing-page-cube {\r\n    width: 100%;\r\n    height: 100vh;\r\n  }\r\n}\r\n\r\n#landing-page {\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  justify-content: center;\r\n  overflow: hidden;\r\n  height: 100vh;\r\n  width: 100vw;\r\n\r\n  &.desktop-about-page-open,\r\n  &.desktop-people-page-open,\r\n  &.desktop-watch-page-open {\r\n    .landing-page-background {\r\n      opacity: 0.65;\r\n      transition-duration: 1s;\r\n    }\r\n\r\n    #main-screen #curr-line.notFaded {\r\n      opacity: 0;\r\n    }\r\n  }\r\n\r\n  &.desktop-people-page-open {\r\n    #main-screen #curr-line {\r\n      opacity: 0;\r\n    }\r\n  }\r\n\r\n  .faded {\r\n    opacity: 0;\r\n    transition: 0.2s;\r\n  }\r\n\r\n  .notFaded {\r\n    opacity: 1;\r\n    transition: 0.2s;\r\n  }\r\n\r\n  #main-screen {\r\n    flex-direction: row;\r\n\r\n    #curr-line {\r\n      position: absolute;\r\n      left: $X_MARGIN/2;\r\n      width: 100vw - $X_MARGIN/2;\r\n      bottom: 40vh;\r\n      display: block;\r\n      opacity: 0;\r\n\r\n      &.notFaded {\r\n        opacity: 1;\r\n        transition: 0.2s;\r\n      }\r\n\r\n      #above-line-name {\r\n        display: flex;\r\n        flex-direction: row;\r\n        align-items: center;\r\n\r\n        #line-name {\r\n          font-family: $FONT_DISPLAY;\r\n          font-size: $FONT_SIZE_LINE;\r\n          text-transform: uppercase;\r\n          font-weight: $FONT_WEIGHT_LIGHT;\r\n          letter-spacing: $FONT_SPRAT_KERN;\r\n          word-spacing: $HEADER_WORD_SPACING;\r\n          pointer-events: none;\r\n        }\r\n\r\n        #see-more-wrapper {\r\n          display: flex;\r\n          flex-grow: 1;\r\n          flex-direction: column;\r\n          align-items: flex-end;\r\n          // TODO: So I think the bug here is that on windows, the font has\r\n          // extra padding on the bottom, and on mac, the padding appears above\r\n          // the font. This means the padding has to be in different spots\r\n          // depending on platform... I think less padding is more correct, but\r\n          // idk why it doesn't work.\r\n          // padding-bottom: $SEE_MORE_BOTTOM_MARGIN;\r\n          margin-left: 36px;\r\n          right: $SEE_MORE_WRAPPER_MARGIN;\r\n\r\n          opacity: 0;\r\n          transition-duration: 1s;\r\n          transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);\r\n\r\n          &.show {\r\n            opacity: 1;\r\n          }\r\n\r\n          #more-info {\r\n            position: absolute;\r\n            display: flex;\r\n            flex-direction: row;\r\n            margin-right: $X_MARGIN/2 - $SEE_MORE_WRAPPER_MARGIN;\r\n            margin-bottom: 12px;\r\n            bottom: 55%;\r\n            text-decoration: none;\r\n            cursor: pointer;\r\n\r\n            #more-info-text {\r\n              font-family: $FONT_SUBHEAD;\r\n              text-transform: uppercase;\r\n              font-size: $FONT_SIZE_LABEL;\r\n              font-weight: $FONT_WEIGHT_LIGHT;\r\n              margin-right: 8px;\r\n            }\r\n\r\n            #arrow {\r\n              width: $ARROW_WIDTH;\r\n              height: $ARROW_WIDTH;\r\n              margin-top: $ARROW_WIDTH - 3px;\r\n              border-right: 2px solid white;\r\n              border-bottom: 2px solid white;\r\n              transform: rotate(-45deg);\r\n            }\r\n          }\r\n\r\n          #see-more-line-wrapper {\r\n            display: flex;\r\n            flex-direction: row;\r\n            align-items: center;\r\n            flex-grow: 1;\r\n            margin-right: $SEE_MORE_WRAPPER_MARGIN;\r\n            // Give a little space between the designer name and the circle\r\n            width: 99%;\r\n            // The font has some bottom spacing built-in, this centers the line\r\n            padding-top: 5px;\r\n            // By default, the line is not visible\r\n            transform: translateX(100%);\r\n            transition-duration: 500ms;\r\n            transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);\r\n\r\n            &.visible {\r\n              transform: translateX(0);\r\n            }\r\n\r\n            #see-more-line {\r\n              display: flex;\r\n              right: $OUTER;\r\n              border-bottom: 1px solid white;\r\n              flex-grow: 1;\r\n              height: 1px;\r\n              top: 1px;\r\n            }\r\n\r\n            // A bit different from other dots, so not going to use their styles\r\n            #see-more-dot {\r\n              bottom: $SEE_MORE_BOTTOM_MARGIN;\r\n              width: $CIRCLE_RAD;\r\n              height: $CIRCLE_RAD;\r\n              background-color: white;\r\n              border-radius: 100%;\r\n            }\r\n          }\r\n        }\r\n      }\r\n\r\n      #below-line-name {\r\n        display: flex;\r\n        flex-direction: row;\r\n\r\n        #designers-name {\r\n          display: flex;\r\n          height: $DESIGNERS_NAME_HEIGHT;\r\n          font-size: $FONT_SIZE_DESIGNER;\r\n          font-family: $FONT_SUBHEAD;\r\n          text-transform: uppercase;\r\n          letter-spacing: $SUBHEAD_TRACKING;\r\n        }\r\n      }\r\n    }\r\n  }\r\n\r\n  .logo {\r\n    position: absolute;\r\n    z-index: $Z_INDEX_HEADER;\r\n\r\n    // Gonna be totally honest I don't think this is _quite_ accurate to get 21px under the logo\r\n    top: $TOP_MARGIN;\r\n    right: $X_MARGIN/2;\r\n    width: 4.6vh;\r\n    height: 6.1vh;\r\n    cursor: pointer;\r\n    transition-duration: 1s;\r\n\r\n    &.start-animation {\r\n      animation: blur-in $ANIMATION_DURATION_BLUR forwards;\r\n    }\r\n    &.desktop-landing-page-load, &.desktop-landing-page-cube-intro, &.desktop-line-page-load {\r\n      opacity: 0;\r\n      pointer-events: none;\r\n    }\r\n    &.desktop-about-page-load, &.desktop-watch-page-load, &.desktop-people-page-load,\r\n    &.desktop-about-page-open, &.desktop-watch-page-open, &.desktop-people-page-open {\r\n      opacity: 0 !important;\r\n      pointer-events: none;\r\n    }\r\n  }\r\n}","/**\r\n * About page for desktop\r\n */\r\n\r\n$BOTTOM_DESCRIPTOR_LINE_WIDTH: 44.3vw;\r\n\r\n@keyframes expand-border-height-first {\r\n  0% {\r\n    width: 0;\r\n    height: 0;\r\n  }\r\n\r\n  50% {\r\n    width: 0;\r\n    height: 100%;\r\n  }\r\n\r\n  100% {\r\n    width: 100%;\r\n    height: 100%;\r\n  }\r\n}\r\n\r\n@keyframes expand-border-width-first {\r\n  0% {\r\n    width: 0;\r\n    height: 0;\r\n  }\r\n\r\n  50% {\r\n    width: 100%;\r\n    height: 0;\r\n  }\r\n\r\n  100% {\r\n    width: 100%;\r\n    height: 100%;\r\n  }\r\n}\r\n\r\n\r\n#about-page-desktop {\r\n  position: absolute;\r\n  display: flex;\r\n  flex-direction: column;\r\n  width: 100vw;\r\n  height: 100vh;\r\n  opacity: 0;\r\n\r\n  &.desktop-about-page-open {\r\n    opacity: 1;\r\n\r\n    // Text blurs in after all sliding animations are done\r\n    #headline #headline-text, #description-text {\r\n      opacity: 0;\r\n      animation: blur-in $ANIMATION_DURATION_BLUR forwards;\r\n      animation-delay: $ANIMATION_DURATION_SLIDE_IN + $ANIMATION_DURATION_SLIDE_IN;\r\n    }\r\n\r\n    #right-bar {\r\n      animation: right-bar-slide-in $ANIMATION_DURATION_SLIDE_IN forwards;\r\n    }\r\n\r\n    #bottom-descriptor {\r\n      .dot-basic,\r\n      .line {\r\n        animation: left-bar-slide-in $ANIMATION_DURATION_SLIDE_IN forwards;\r\n      }\r\n\r\n      #description {\r\n        #border-left-bottom {\r\n          position: absolute;\r\n          top: 0;\r\n          left: 0;\r\n          border-bottom: 1px solid white; // Make borders visible\r\n          border-left: 1px solid white; // Make borders visible\r\n          animation: expand-border-height-first $ANIMATION_DURATION_SLIDE_IN forwards;\r\n          animation-delay: $ANIMATION_DURATION_SLIDE_IN;\r\n        }\r\n\r\n        // And this the bottom & left borders (expands left, then up)\r\n        #border-top-right {\r\n          position: absolute;\r\n          top: 0;\r\n          left: 0;\r\n          border-top: 1px solid white; // Make borders visible\r\n          border-right: 1px solid white; // Make borders visible\r\n          animation: expand-border-width-first $ANIMATION_DURATION_SLIDE_IN forwards;\r\n          animation-delay: $ANIMATION_DURATION_SLIDE_IN;\r\n        }\r\n      }\r\n    }\r\n  }\r\n\r\n  #headline {\r\n    display: flex;\r\n    flex-direction: row;\r\n    margin-top: 20vh;\r\n\r\n    #headline-text {\r\n      margin-left: $X_MARGIN/2;\r\n      font-family: 'Sprat Type Condensed';\r\n      font-weight: 100;\r\n      letter-spacing: -0.12em;\r\n      font-size: 5.6vw;\r\n      width: 45vw;\r\n      word-spacing: 0.1em;\r\n    }\r\n\r\n    #right-bar {\r\n      display: flex;\r\n      flex-direction: row;\r\n      align-items: center;\r\n      flex-grow: 1;\r\n      margin-left: 36px;\r\n\r\n      .dot-basic {\r\n        display: flex;\r\n      }\r\n\r\n      .line {\r\n        display: flex;\r\n        flex-grow: 1;\r\n      }\r\n    }\r\n  }\r\n\r\n  #bottom-descriptor {\r\n    display: flex;\r\n    flex-direction: row;\r\n    align-items: center;\r\n    position: absolute;\r\n    bottom: 7vh;\r\n    width: 100vw;\r\n\r\n    #left-bar {\r\n      display: flex;\r\n      flex-direction: column;\r\n      align-items: center;\r\n      justify-content: center;\r\n      min-width: $BOTTOM_DESCRIPTOR_LINE_WIDTH;\r\n\r\n      .dot-basic {\r\n        display: flex;\r\n      }\r\n\r\n      .line {\r\n        position: absolute;\r\n        width: $BOTTOM_DESCRIPTOR_LINE_WIDTH;\r\n      }\r\n    }\r\n\r\n    #description {\r\n      position: relative;\r\n      padding-top: 48px;\r\n      padding-bottom: 48px;\r\n      padding-left: 65px;\r\n      padding-right: 45px;\r\n      margin-right: $X_MARGIN*3/8;\r\n      z-index: $Z_INDEX_HEADER;\r\n\r\n      #description-text {\r\n        font-family: $FONT_BODY;\r\n        font-size: 18px;\r\n      }\r\n    }\r\n  }\r\n}","/**\r\n * Watch page for desktop. Similar to the about page where it is not a\r\n * standalone page, and lives inside the landing page. Its visibility is\r\n * controlled by the landing page state.\r\n */\r\n\r\n$MARGIN_HORIZONTAL_COUNTDOWN: 10vw;\r\n$SUBTEXT_LETTER_SPACING: -0.07em;\r\n\r\n#watch-page-desktop {\r\n  position: absolute;\r\n  display: flex;\r\n  flex-direction: column;\r\n  justify-content: center;\r\n  align-items: center;\r\n  width: 100vw;\r\n  height: 100vh;\r\n  opacity: 0;\r\n\r\n  .left-bar {\r\n    transform: translateX(-100%);\r\n  }\r\n\r\n  &.desktop-watch-page-open {\r\n    opacity: 1;\r\n\r\n    .right-bar {\r\n      animation: right-bar-slide-in $ANIMATION_DURATION_SLIDE_IN forwards;\r\n    }\r\n\r\n    .left-bar {\r\n      animation: left-bar-slide-in $ANIMATION_DURATION_SLIDE_IN forwards;\r\n      animation-delay: $ANIMATION_DURATION_SLIDE_IN;\r\n    }\r\n\r\n    #countdown, #show-date-text, #show-time-text {\r\n      opacity: 0;\r\n      animation: blur-in $ANIMATION_DURATION_BLUR forwards;\r\n      animation-delay: $ANIMATION_DURATION_SLIDE_IN*2;\r\n    }\r\n  }\r\n\r\n  #countdown {\r\n    display: flex;\r\n    flex-direction: row;\r\n    justify-content: space-between;\r\n    width: 100vw - 2*$MARGIN_HORIZONTAL_COUNTDOWN;\r\n    margin-right: $MARGIN_HORIZONTAL_COUNTDOWN;\r\n    margin-left: $MARGIN_HORIZONTAL_COUNTDOWN;\r\n\r\n    .countdown-component {\r\n      display: flex;\r\n      flex-direction: column;\r\n      align-items: center;\r\n      width: 25%;\r\n\r\n      .number {\r\n        font-family: 'Sprat Type Condensed';\r\n        font-size: 14.4vw;\r\n        font-weight: 300;\r\n        letter-spacing: $SUBTEXT_LETTER_SPACING;\r\n      }\r\n\r\n      .label {\r\n        font-family: $FONT_SUBHEAD;\r\n        font-size: 2.36vw;\r\n        font-weight: 400;\r\n        letter-spacing: $FONT_SPRAT_KERN;\r\n      }\r\n    }\r\n  }\r\n\r\n  #show-date {\r\n    position: absolute;\r\n    display: flex;\r\n    flex-direction: row;\r\n    justify-content: center;\r\n    align-items: center;\r\n    bottom: 8vw;\r\n    width: 100vw;\r\n\r\n    #show-date-text {\r\n      font-family: 'Sprat Type Condensed';\r\n      font-size: 4.44vw;\r\n      font-weight: 300;\r\n      margin-left: $X_MARGIN*4/8;\r\n      margin-right: 12px;\r\n      backface-visibility: hidden;\r\n    }\r\n\r\n    .right-bar {\r\n      display: flex;\r\n      flex-direction: row;\r\n      align-items: center;\r\n      flex-grow: 1;\r\n\r\n      .line {\r\n        flex-grow: 1;\r\n        background-color: white;\r\n        height: 1px;\r\n      }\r\n    }\r\n  }\r\n\r\n  #show-time {\r\n    position: absolute;\r\n    display: flex;\r\n    flex-direction: row;\r\n    align-items: center;\r\n    bottom: 3vw;\r\n    width: 100vw;\r\n\r\n    #show-time-text {\r\n      font-family: 'Sprat Type Condensed';\r\n      font-size: 4.44vw;\r\n      font-weight: 300;\r\n      letter-spacing: $SUBTEXT_LETTER_SPACING;\r\n      margin-right: $X_MARGIN*4/8;\r\n      margin-left: 12px;\r\n      backface-visibility: hidden;\r\n    }\r\n\r\n    .left-bar {\r\n      display: flex;\r\n      flex-direction: row;\r\n      align-items: center;\r\n      flex-grow: 1;\r\n\r\n      .line {\r\n        flex-grow: 1;\r\n        background-color: white;\r\n        height: 1px;\r\n      }\r\n    }\r\n  }\r\n}","/**\r\n * Landing page navbar\r\n */\r\n\r\n$UNDERLINE_CIRCLE_WIDTH: 8px;\r\n$UNDERLINE_WIDTH: 32px + 2*$UNDERLINE_CIRCLE_WIDTH;\r\n$UNDERLINE_CIRCLE_TRANSLATE_X: $UNDERLINE_WIDTH/2;\r\n$NAVBALL_SIZE: 10px;\r\n\r\n@keyframes underline-animation {\r\n  from {\r\n    width: 0px;\r\n  } to {\r\n    width: $UNDERLINE_WIDTH;\r\n  }\r\n}\r\n\r\n@keyframes underline-circle-left-animation {\r\n  from {\r\n    transform: translateX(0);\r\n  } to {\r\n    transform: translateX(-$UNDERLINE_CIRCLE_TRANSLATE_X);\r\n  }\r\n}\r\n\r\n@keyframes underline-circle-right-animation {\r\n  from {\r\n    transform: translateX(0);\r\n  } to {\r\n    transform: translateX($UNDERLINE_CIRCLE_TRANSLATE_X);\r\n  }\r\n}\r\n\r\n.navbar {\r\n  display: flex;\r\n  flex-direction: column;\r\n  justify-content: center;\r\n  align-items: center;\r\n  width: 100vw;\r\n  // Put this navbar at the bottom\r\n  position: fixed;\r\n  bottom: $OUTER;\r\n\r\n  border-top: 1px solid white;\r\n  border-bottom: 1px solid white;\r\n  padding-top: 2.5vh;\r\n  padding-bottom: 2.5vh;\r\n  transition-duration: 1s;\r\n  pointer-events: none;\r\n  z-index: $Z_INDEX_HEADER;\r\n\r\n  &.default, &.desktop-line-page-open {\r\n    pointer-events: all;\r\n    .navbar-container {\r\n      animation: blur-in $ANIMATION_DURATION_BLUR forwards;\r\n    }\r\n  }\r\n  &.desktop-landing-page-load {\r\n    opacity: 0;\r\n    pointer-events: none;;\r\n\r\n    // We need to keep this because the navbar will have opacity: 1 at the end\r\n    // of its animation\r\n    .navbar-container {\r\n      opacity: 0;\r\n      pointer-events: none;;\r\n    }\r\n  }\r\n  &.desktop-landing-page-cube-intro {\r\n    .navbar-container {\r\n      opacity: 0;\r\n      pointer-events: none;;\r\n    }\r\n  }\r\n  &.desktop-about-page-open, &.desktop-watch-page-open, &.desktop-people-page-open {\r\n    opacity: 0;\r\n    pointer-events: none;;\r\n  }\r\n  &.desktop-about-page-load, &.desktop-watch-page-load, &.desktop-people-page-load, &.desktop-line-page-load {\r\n    opacity: 0;\r\n    pointer-events: none;;\r\n  }\r\n\r\n  .navbar-container {\r\n    display: flex;\r\n    flex-direction: row;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n    width: 100vw - 2*3*$OUTER;\r\n    transition-duration: 1s;\r\n  }\r\n\r\n  .navBall {\r\n    margin-bottom: 6px;\r\n    display: block;\r\n    width: $NAVBALL_SIZE;\r\n    height: $NAVBALL_SIZE;\r\n    background-color: white;\r\n    border-radius: 100%;\r\n    opacity: 0;\r\n    transition: .5s;\r\n  }\r\n\r\n  .underline-circle {\r\n    position: absolute;\r\n    width: $UNDERLINE_CIRCLE_WIDTH;\r\n    height: $UNDERLINE_CIRCLE_WIDTH;\r\n    background-color: white;\r\n    border-radius: 100%;\r\n  }\r\n\r\n  .underline {\r\n    position: absolute;\r\n    width: 0px;\r\n    height: 1px;\r\n    background-color: white;\r\n  }\r\n\r\n  .navbar-item {\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n    font-size: $FONT_SIZE_NAV;\r\n    cursor: pointer;\r\n    font-weight: $FONT_SPRAT_WEIGHT;\r\n    transition: .5s;\r\n    border-bottom: 2px solid;\r\n    border-color: rgba(255, 255, 255, 0);\r\n    padding-bottom: $BOTTOM_MARGIN*0.05;\r\n\r\n    .underline-animation {\r\n      margin-top: 4px;\r\n      display: flex;\r\n      align-items: center;\r\n      justify-content: center;\r\n      width: 100%;\r\n      height: 12px;\r\n      opacity: 0;\r\n      transition: $MEDIUM_ANIMATION_DURATION;\r\n    }\r\n\r\n    &:hover .navBall {\r\n      opacity: 1;\r\n    }\r\n\r\n    &.selected {\r\n      .navBall {\r\n        opacity: 1;\r\n      }\r\n\r\n      .underline-animation {\r\n        opacity: 1;\r\n\r\n        .left {\r\n          animation: $MEDIUM_ANIMATION_DURATION ease-in-out underline-circle-left-animation forwards;\r\n        }\r\n\r\n        .right {\r\n          animation: $MEDIUM_ANIMATION_DURATION ease-in-out underline-circle-right-animation forwards;\r\n        }\r\n\r\n        .underline {\r\n          animation: $MEDIUM_ANIMATION_DURATION ease-in-out underline-animation forwards;\r\n        }\r\n      }\r\n    }\r\n  }\r\n}\r\n","/**\r\n * Line page navbar\r\n */\r\n\r\n$UNDERLINE_CIRCLE_WIDTH: 8px;\r\n$UNDERLINE_WIDTH: 32px + 2*$UNDERLINE_CIRCLE_WIDTH;\r\n$UNDERLINE_CIRCLE_TRANSLATE_X: $UNDERLINE_WIDTH/2;\r\n$TOOLTIP_CIRCLE_WIDTH: 9px;\r\n$TOOLTIP_CIRCLE_HEIGHT: 9px;\r\n$TOOLTIP_MARGIN: 10px;\r\n$TOOLTIP_TICK_LENGTH: 4.5vh;\r\n$NAVBALL_SIZE: 10px;\r\n\r\n@keyframes underline-animation {\r\n  from {\r\n    width: 0px;\r\n  } to {\r\n    width: $UNDERLINE_WIDTH;\r\n  }\r\n}\r\n\r\n@keyframes underline-circle-left-animation {\r\n  from {\r\n    transform: translateX(0);\r\n  } to {\r\n    transform: translateX(-$UNDERLINE_CIRCLE_TRANSLATE_X);\r\n  }\r\n}\r\n\r\n@keyframes underline-circle-right-animation {\r\n  from {\r\n    transform: translateX(0);\r\n  } to {\r\n    transform: translateX($UNDERLINE_CIRCLE_TRANSLATE_X);\r\n  }\r\n}\r\n\r\n.navbar-line-page {\r\n  display: flex;\r\n  flex-direction: column;\r\n  justify-content: center;\r\n  align-items: center;\r\n  width: 100vw;\r\n\r\n  border-top: 1px solid white;\r\n  border-bottom: 1px solid white;\r\n\r\n  position: fixed;\r\n  bottom: 2.5vh;\r\n  padding-top: 2.5vh;\r\n  padding-bottom: 2.5vh;\r\n\r\n  .navbar-container {\r\n    display: flex;\r\n    flex-direction: row;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n    width: 100vw - 2*3*$OUTER;\r\n  }\r\n\r\n  .navBall {\r\n    margin-bottom: 6px;\r\n    display: block;\r\n    width: $NAVBALL_SIZE;\r\n    height: $NAVBALL_SIZE;\r\n    background-color: white;\r\n    border-radius: 100%;\r\n    opacity: 0;\r\n    transition: .5s;\r\n  }\r\n\r\n  .underline-circle {\r\n    position: absolute;\r\n    width: $UNDERLINE_CIRCLE_WIDTH;\r\n    height: $UNDERLINE_CIRCLE_WIDTH;\r\n    background-color: white;\r\n    border-radius: 100%;\r\n  }\r\n\r\n  .underline {\r\n    position: absolute;\r\n    width: 0px;\r\n    height: 1px;\r\n    background-color: white;\r\n  }\r\n\r\n  .navbar-item {\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n    font-size: $FONT_SIZE_NAV;\r\n    cursor: pointer;\r\n    font-weight: $FONT_SPRAT_WEIGHT;\r\n    transition: .5s;\r\n    border-bottom: 2px solid;\r\n    border-color: rgba(255, 255, 255, 0);\r\n    padding-bottom: $BOTTOM_MARGIN*0.05;\r\n    transition: .5s;\r\n    text-decoration: none;\r\n\r\n    .underline-animation {\r\n      margin-top: 4px;\r\n      display: flex;\r\n      align-items: center;\r\n      justify-content: center;\r\n      width: 100%;\r\n      height: 12px;\r\n      opacity: 0;\r\n      transition: $MEDIUM_ANIMATION_DURATION;\r\n    }\r\n\r\n    &.selected {\r\n      .navBall {\r\n        opacity: 1;\r\n      }\r\n\r\n      .underline-animation {\r\n        opacity: 1;\r\n\r\n        .left {\r\n          animation: $MEDIUM_ANIMATION_DURATION ease-in-out underline-circle-left-animation forwards;\r\n        }\r\n\r\n        .right {\r\n          animation: $MEDIUM_ANIMATION_DURATION ease-in-out underline-circle-right-animation forwards;\r\n        }\r\n\r\n        .underline {\r\n          animation: $MEDIUM_ANIMATION_DURATION ease-in-out underline-animation forwards;\r\n        }\r\n      }\r\n    }\r\n\r\n    &:hover .tooltip {\r\n      opacity: 1;\r\n    }\r\n\r\n    // Special margin rule for the first line since it doesn't fit\r\n    &#line-0 .tooltip .wrapper {\r\n      margin-left: 82px;\r\n\r\n      #left-top {\r\n        margin-left: 82px;\r\n      }\r\n    }\r\n\r\n    .tooltip {\r\n      display: flex;\r\n      flex-direction: column;\r\n      align-items: center;\r\n      position: absolute;\r\n      opacity: 0;\r\n      margin-bottom: 40px;\r\n      bottom: 63px;\r\n      transition-duration: 200ms;\r\n\r\n      .vertical-tick {\r\n        width: 1px;\r\n        height: $TOOLTIP_TICK_LENGTH;\r\n        border-right: 1px solid white;\r\n      }\r\n\r\n      .wrapper {\r\n        display: flex;\r\n        flex-direction: row;\r\n        justify-content: center;\r\n        border: 1px solid white;\r\n        padding-left: 10px;\r\n        padding-right: 10px;\r\n        background-color: rgba(0, 0, 0, 0.5);\r\n\r\n        .label {\r\n          border-left: 1px solid white;\r\n          border-right: 1px solid white;\r\n          font-family: $FONT_SUBHEAD;\r\n          font-weight: 400;\r\n          font-size: 18px;\r\n          text-transform: uppercase;\r\n          white-space: nowrap;\r\n\r\n          padding-top: 8px;\r\n          padding-bottom: 6px;\r\n          padding-left: $TOOLTIP_MARGIN;\r\n          padding-right: $TOOLTIP_MARGIN;\r\n        }\r\n\r\n        .dot-basic {\r\n          width: $TOOLTIP_CIRCLE_WIDTH;\r\n          height: $TOOLTIP_CIRCLE_HEIGHT;\r\n          position: absolute;\r\n        }\r\n\r\n        #left-top {\r\n          top: -$TOOLTIP_CIRCLE_WIDTH/2 + 1;\r\n          left: $TOOLTIP_CIRCLE_WIDTH/2 + 2;\r\n        }\r\n\r\n        #right-bottom {\r\n          right: $TOOLTIP_CIRCLE_WIDTH/2 + 1;\r\n          bottom: calc(#{$TOOLTIP_TICK_LENGTH} - #{$TOOLTIP_CIRCLE_WIDTH/2} + 1px);\r\n        }\r\n      }\r\n\r\n      &.inactive {\r\n        opacity: 0;\r\n      }\r\n    }\r\n  }\r\n}\r\n","/**\r\n * Navigation sidebar on desktop\r\n */\r\n\r\n$DESKTOP_SIDE_NAV_SLIDE_DURATION: 400ms;\r\n\r\n@keyframes left-bar-slide-in-desktop-nav {\r\n  from {\r\n    transform: translateX(-50%);\r\n  }\r\n\r\n  to {\r\n    transform: translateX(0);\r\n  }\r\n}\r\n\r\n.links {\r\n  position: fixed;\r\n  top: $TOP_MARGIN;\r\n  left: 0;\r\n  transition-duration: 1s;\r\n  opacity: 0;\r\n  z-index: $Z_INDEX_HEADER;\r\n\r\n  &.start-animation {\r\n    animation: blur-in $ANIMATION_DURATION_BLUR forwards;\r\n  }\r\n\r\n  &.desktop-landing-page-load {\r\n    opacity: 0;\r\n    pointer-events: none;;\r\n  }\r\n\r\n  &.desktop-landing-page-cube-intro {\r\n    opacity: 0;\r\n    pointer-events: none;;\r\n  }\r\n\r\n  &.line-page {\r\n    opacity: 1;\r\n  }\r\n\r\n  .link-wrapper {\r\n    display: flex;\r\n    flex-direction: row;\r\n    margin-bottom: 4px; //changed from 8px\r\n    transition-duration: 1s;\r\n\r\n    &.selected .left-bar {\r\n      animation: left-bar-slide-in-desktop-nav $DESKTOP_SIDE_NAV_SLIDE_DURATION forwards cubic-bezier(0.075, 0.82, 0.165, 1);\r\n      opacity: 1;\r\n    }\r\n\r\n    &:hover .left-bar {\r\n      animation: left-bar-slide-in-desktop-nav $DESKTOP_SIDE_NAV_SLIDE_DURATION forwards cubic-bezier(0.075, 0.82, 0.165, 1);\r\n      opacity: 1;\r\n    }\r\n\r\n    .left-bar {\r\n      display: flex;\r\n      flex-direction: row;\r\n      align-items: center;\r\n      opacity: 0;\r\n      transform: translateX(-100%);\r\n\r\n      .line {\r\n        display: flex;\r\n        flex-grow: 1;\r\n        padding-right: $X_MARGIN/4;\r\n      }\r\n\r\n      .dot-basic {\r\n        margin-left: -$CIRCLE_RAD/2;\r\n        margin-right: 14px;\r\n      }\r\n    }\r\n\r\n    .link {\r\n      font-family: $FONT_BODY;\r\n      font-size: $FONT_SIZE_LINK;\r\n      text-transform: uppercase;\r\n      font-weight: $FONT_LINK_WEIGHT;\r\n      display: block;\r\n      color: white;\r\n      text-decoration: none;\r\n    }\r\n  }\r\n}","/**\r\n * Prompt that appears after the initial load and before a user enters the main\r\n * site.\r\n */\r\n\r\n$MARGIN_BAR: 12px;\r\n$BAR_WIDTH: 74vw;\r\n$TITLE_FONT_SIZE: 14vh;\r\n$LOGO_SIZE: $TITLE_FONT_SIZE - 3vh;\r\n\r\n#landing-page-prompt {\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  width: 100vw;\r\n  position: absolute;\r\n  opacity: 0;\r\n  pointer-events: none;\r\n\r\n  &.hidden {\r\n    display: none;\r\n  }\r\n\r\n  .bar {\r\n    display: flex;\r\n    flex-direction: row;\r\n    align-items: center;\r\n    width: 100vw;\r\n\r\n    .line {\r\n      display: flex;\r\n      flex-grow: 1;\r\n    }\r\n\r\n    .hidden {\r\n      width: 100vw - $BAR_WIDTH;\r\n    }\r\n  }\r\n\r\n  .right-bar {\r\n    margin-bottom: $MARGIN_BAR;\r\n  }\r\n\r\n  .left-bar {\r\n    margin-top: $MARGIN_BAR;\r\n  }\r\n\r\n  #title {\r\n    display: flex;\r\n    flex-direction: row;\r\n    align-items: center;\r\n\r\n    span {\r\n      font-size: $TITLE_FONT_SIZE;\r\n      font-weight: 300;\r\n      letter-spacing: $FONT_SPRAT_KERN;\r\n    }\r\n\r\n    #collectiva-logo {\r\n      margin-top: 12px;\r\n      margin-left: 12px;\r\n      height: $LOGO_SIZE;\r\n      width: $LOGO_SIZE;\r\n\r\n      svg {\r\n        height: 100%;\r\n        width: 100%;\r\n      }\r\n    }\r\n  }\r\n\r\n  #enter-site {\r\n    margin-top: 12px;\r\n    font-family: $FONT_SUBHEAD;\r\n    font-size: 24px;\r\n    cursor: pointer;\r\n  }\r\n}","/**\r\n * Styles for the page showing each individual line\r\n */\r\n\r\n$LEFT_MARGIN: 20vw;\r\n$TITLE_MARGIN_TOP: 15vh;\r\n\r\n$SLIDE_AMOUNT_A: 40px;\r\n$SLIDE_AMOUNT_B: 40px;\r\n$BACK_BUTTON_SIZE: 24px;\r\n$BACK_BUTTON_STROKE_WIDTH: 3px;\r\n$LOGO_SIZE: $FONT_SIZE_TITLE - 8px;\r\n\r\n$PICTURE_WIDTH: 16vw;\r\n$PICTURE_HEIGHT: 40vh;\r\n$PICTURE_LEFT_MARGIN: 3vw;\r\n\r\n// Animations\r\n@keyframes slide-image-a {\r\n  from {\r\n    transform: translateY(0);\r\n  }\r\n\r\n  to {\r\n    transform: translateY($SLIDE_AMOUNT_A);\r\n  }\r\n}\r\n\r\n@keyframes slide-image-b {\r\n  from {\r\n    transform: translateY(0);\r\n  }\r\n\r\n  to {\r\n    transform: translateY($SLIDE_AMOUNT_B);\r\n  }\r\n}\r\n\r\n.blur {\r\n  o