 100%;\r\n          margin-bottom: 1px;\r\n        }\r\n      }\r\n    }\r\n  }\r\n}\r\n","/**\r\n * The people page for mobile\r\n */\r\n\r\n$TAB_TEXT_SIZE: 36px;\r\n$HEIGHT_TO_WIDTH_RATIO_HEADSHOT: 1.21;\r\n$SIDE_MARGIN: $X_MARGIN/2;\r\n$HEADSHOT_WIDTH: (100vw - 3*$SIDE_MARGIN)/2;\r\n\r\n@keyframes slide-in {\r\n  from {\r\n    transform: translateX(100vw);\r\n  }\r\n\r\n  to {\r\n    transform: translateX(0);\r\n  }\r\n}\r\n\r\n@keyframes slide-out {\r\n  from {\r\n    transform: translateX(0);\r\n  }\r\n\r\n  to {\r\n    transform: translateX(100vw);\r\n  }\r\n}\r\n\r\n#people-page {\r\n  overflow-x: hidden;\r\n}\r\n\r\n#people-page.main-page {\r\n  display: none;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  // Enable scroll on this page\r\n  overflow-y: auto;\r\n  position: absolute;\r\n  width: 100vw;\r\n  height: 100%;\r\n  top: $LANDING_PAGE_HEADER_HEIGHT;\r\n\r\n  &.mobile-people-page-open {\r\n    display: flex;\r\n\r\n    .page-background,\r\n    #team-accordion {\r\n      animation: blur-in $ANIMATION_DURATION_BLUR forwards;\r\n      backface-visibility: hidden;\r\n      z-index: $Z_INDEX_HEADER;\r\n    }\r\n  }\r\n\r\n  .headshotImg {\r\n    background-size: cover;\r\n    background-repeat: no-repeat;\r\n    background-blend-mode: color;\r\n  }\r\n\r\n  .headshot {\r\n    width: $HEADSHOT_WIDTH;\r\n    height: $HEIGHT_TO_WIDTH_RATIO_HEADSHOT*$HEADSHOT_WIDTH;\r\n    background-size: cover;\r\n    background-repeat: no-repeat;\r\n    background-blend-mode: color;\r\n  }\r\n\r\n  .wrapper {\r\n    position: fixed;\r\n    top: 0;\r\n    left: 0;\r\n    right: 0;\r\n    height: 30vh;\r\n    background: linear-gradient(180deg, rgba(20, 20, 20, 1) 40%, rgba(20, 20, 20, 0) 100%);\r\n    z-index: 100;\r\n    padding: 0;\r\n\r\n    .header {\r\n      padding: 0;\r\n      margin: 0;\r\n      display: flex;\r\n      flex-direction: column;\r\n      align-items: center;\r\n    }\r\n  }\r\n\r\n  .title-theme {\r\n    position: fixed;\r\n    font-family